
import { defineComponent, ref } from "vue";
import { CollectionsOverviewType } from "@/interefaces/Images";
import imageCard from "@/components/imageCard.vue";
import { apiImagesOverview, apiUserGet } from "@/API";

export default defineComponent({
  name: "Catalog",
  components: {
    imageCard,
  },
  setup() {
    const cardItemList = ref<CollectionsOverviewType[]>([]);

    return {
      cardItemList,
    };
  },
  data() {
    return {
      currentDate: new Date(),
      options: [
        {
          value: "Description",
          label: "Description",
        },
        {
          value: "Display Name",
          label: "Display Name",
        },
        {
          value: "Name",
          label: "Name",
        },
        {
          value: "Publisher",
          label: "Publisher",
        },
      ],
      value: [],
    };
  },
  methods: {
    judge() {
      return screen.width / 250;
    },
  },
  mounted() {
    apiImagesOverview().then((res) => {
      console.log(res);
      this.cardItemList = res.data;
      console.log(this.cardItemList);
    });
    apiUserGet().then((res) => {
      console.log("Get", res);
    });
  },
});
